// @flow
import React, { useState, Fragment } from 'react';
import type { Element } from 'react';
import type { Offer } from 'types/Offer';
import { IconButton, Typography, Menu, MenuItem } from '@material-ui/core';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';

type Props = {
  offer: Offer,
  handleScheduleInspection: Function,
};

export const ActionButton = ({
  offer,
  handleScheduleInspection,
}: Props): Element<typeof Fragment> => {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const onClickConfirmHandler = () => {
    handleScheduleInspection(offer);
  };

  return (
    <>
      <IconButton
        aria-controls="simple-menu"
        aria-haspopup="true"
        type="button"
        onClick={handleClick}
      >
        <MoreHorizIcon />
      </IconButton>
      <Menu
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem component="button" onClick={onClickConfirmHandler}>
          <Typography variant="h4">Schedule Inspection</Typography>
        </MenuItem>
      </Menu>
    </>
  );
};

export default ActionButton;
