import { gql } from '@apollo/client';

const LIST_PROPERTY = gql`
  query listProperties {
    listProperties {
      PropertyId
      UpdatedAt
      Thumbnail
      ListingDetails {
        ListPrice
      }
      PropertyData {
        FullAddress
      }
      AcceptedOffer {
        Documents {
          id
          name
          documentType
        }
        OfferId
      }
      PropertyNo
      StatusId
    }
  }
`;

export { LIST_PROPERTY };
