// @flow
import React from 'react';
import type { Element } from 'react';
import Loadable from 'react-loadable';
import _get from 'lodash/get';
import { OFFER_STEPS, OFFER_STEPS_SUBMIT } from 'shared/Consts';
import type { Offer } from 'types/Offer';
import Step1Action from 'components/BuyerOfferStep/Step1/ActionButton';
import Step220Action from 'components/BuyerOfferStep/Step220/NewActionButton';
import Step500Action from 'components/BuyerOfferStep/Step500/ActionButton';
import Step600Action from 'components/BuyerOfferStep/Step600/ActionButton';
import Step700Action from 'components/BuyerOfferStep/Step700/ActionButton';

const actionComponent = {
  step1: Step1Action,
  step220: Step220Action,
  step500: Step500Action,
  step600: Step600Action,
  step700: Step700Action,
};

type Props = {
  offer: Offer,
  handleOpenModalConfirmTransfer?: Function,
  handleScheduleInspection?: Function,
  handleViewOffer?: Function,
};

const ActionButtons = (props: Props): Element<typeof Loadable | 'div'> => {
  const stepId = _get(props, ['offer', 'StepId']);

  // Combine step 1 with step 150
  const componentId = OFFER_STEPS_SUBMIT.includes(stepId)
    ? OFFER_STEPS.pleaseComplete
    : stepId;

  const Component = actionComponent[`step${componentId}`];

  return Component ? <Component {...props} /> : <div>-</div>;
};

export default ActionButtons;
