// @flow
import type { Element } from 'react';
import React from 'react';
import _get from 'lodash.get';
import { Button, Grid, makeStyles } from '@material-ui/core';
import { OFFER_STEPS } from '../../../shared/Consts';
import { Fragment } from 'react';

const useStyles = makeStyles(theme => ({
  wrapper: {
    backgroundColor: theme.palette.background.lightGray,
    justifyContent: 'space-between',
    padding: '1rem',
  },
  acceptButton: {
    borderRadius: 0,
    padding: '0.5625rem 2rem',
    color: theme.palette.common.white,
    borderWidth: '2px',
  },
  declineButton: {
    borderRadius: 0,
    padding: '0.5625rem 2rem',
    color: theme.palette.error.dark,
  },
  counterButton: {
    borderRadius: 0,
    padding: '0.5625rem 2rem',
    borderColor: theme.palette.common.black,
    borderWidth: '2px',
    '&:hover': {
      borderWidth: '2px',
    },
  },
}));

type Props = {
  offer: Object,
  onAccept: Function,
  onDecline: Function,
  onCounter: Function,
  disabled: boolean,
};

export const ViewOfferActionButtons = ({
  offer,
  onAccept,
  onDecline,
  onCounter,
  disabled,
}: Props): Element<typeof Grid | typeof Fragment> => {
  const classes = useStyles();
  const stepId = _get(offer, 'StepId', 0);
  const offerId = _get(offer, 'OfferId', '');

  const isReviewOffer = stepId === OFFER_STEPS.awaitingSellerAction;
  const isReviewCounterOffer = stepId === OFFER_STEPS.buyerAcceptCounterOffer;

  if (!isReviewOffer && !isReviewCounterOffer) return <></>;

  return (
    <Grid container alignItems="center" className={classes.wrapper}>
      <Grid item>
        <Button
          variant="outlined"
          color="primary"
          className={classes.counterButton}
          onClick={() => onCounter(offer)}
          disabled={disabled}
        >
          Counter
        </Button>
      </Grid>
      {isReviewOffer && (
        <Grid item>
          <Button
            className={classes.declineButton}
            onClick={() => onDecline(offerId)}
            disabled={disabled}
          >
            Decline
          </Button>
          <Button
            variant="contained"
            color="primary"
            disableElevation
            className={classes.acceptButton}
            onClick={() => onAccept(offerId)}
            disabled={disabled}
          >
            Accept
          </Button>
        </Grid>
      )}
    </Grid>
  );
};
