import axios from 'axios';
import { Auth } from 'aws-amplify';
import { GetRequestConfig } from '../shared/Consts';
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';
import _omit from 'lodash/omit';

axios.defaults.headers.get['Accept'] = 'application/json';
axios.interceptors.request.use(
  async config => {
    let configHeaders = config.headers;
    if (isAuthorizationRequired(config)) {
      const { headers } = await GetRequestConfig(Auth);
      configHeaders = {
        ...configHeaders,
        ...headers,
      };
    } else {
      configHeaders = _omit(configHeaders, ['Authorization']);
    }
    return {
      ...config,
      headers: configHeaders,
    };
  },
  function (error) {
    return Promise.reject(error);
  },
);
export default axios;

const isAuthorizationRequired = config => {
  const authorization = _get(config, ['headers', 'Authorization'], 'required');
  return !_isEmpty(authorization);
};
