// @flow
import React, { useState, Fragment } from 'react';
import type { Element } from 'react';
import _get from 'lodash/get';
import { FORM_TYPE_IDS, OFFER_STEPS } from 'shared/Consts';
import type { Offer } from 'types/Offer';
import { IconButton, Typography, Menu, MenuItem } from '@material-ui/core';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import { Link } from 'react-router-dom';

type Props = {
  offer: Offer,
};

export const ActionButton = ({ offer }: Props): Element<typeof Fragment> => {
  const [anchorEl, setAnchorEl] = useState(null);
  const offerId = _get(offer, ['OfferId']);
  const scarForm = _get(offer, ['Forms']).find(
    form => _get(form, ['FormTypeId']) === FORM_TYPE_IDS.scar,
  );
  const formId = _get(scarForm, ['FormId']);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const scarUrl = `/offer?offerId=${offerId}&step=${OFFER_STEPS.submitCorrectiveAction}&formId=${formId}`;

  return (
    <>
      <IconButton
        aria-controls="simple-menu"
        aria-haspopup="true"
        type="button"
        onClick={handleClick}
      >
        <MoreHorizIcon />
      </IconButton>
      <Menu
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem>
          <Link to={scarUrl}>
            <Typography variant="h4">Submit SCAR</Typography>
          </Link>
        </MenuItem>
      </Menu>
    </>
  );
};

export default ActionButton;
