// @flow
import _get from 'lodash/get';
import { formatPrice } from '../../../helpers/number';
import type { Offer, OfferInfo } from 'types/Offer';

export const headerCells = [
  {
    id: 'OfferNo',
    numeric: false,
    disablePadding: true,
    label: 'OfferNo',
  },
  {
    id: 'MappingCounter.Price',
    numeric: false,
    disablePadding: true,
    label: 'Offer Price',
  },
  {
    id: 'Funding.PurchaseType',
    numeric: false,
    disablePadding: true,
    label: 'Funding',
  },
  {
    id: 'MappingCounter.InspectionContingency',
    numeric: false,
    disablePadding: true,
    label: 'Due Diligence Contingency',
  },
  {
    id: 'MappingCounter.SaleOfHomeContingency',
    numeric: false,
    disablePadding: true,
    label: 'Sale of Home Contingency',
  },
  {
    id: 'Step.messages.buyer',
    numeric: false,
    disablePadding: true,
    label: 'Status',
  },
];

export const getOfferInfo = (offer: Offer): OfferInfo => ({
  offerId: _get(offer, 'OfferId', ''),
  offerNo: _get(offer, 'OfferNo', ''),
  price: formatPrice(_get(offer, 'MappingCounter.OfferPrice', '')),
  funding: _get(offer, 'Funding.PurchaseType', ''),
  inspectionContingency: _get(
    offer,
    'MappingCounter.InspectionContingency',
    '',
  ),
  saleOfHomeContingency: _get(
    offer,
    'MappingCounter.SaleOfHomeContingency',
    '',
  ),
  status: _get(offer, 'Step.messages.buyer', ''),
});
