import React, { useContext } from 'react';
import UserContext from 'contexts/UserContext';
import SellerOfferList from 'containers/SellerOffer/NewOfferList';
import BuyerOfferList from 'containers/BuyerOffer/NewOfferList';

export const OfferPage = props => {
  const { IsSeller: isSeller } = useContext(UserContext);

  return isSeller ? <SellerOfferList /> : <BuyerOfferList />;
};
