// @flow
import { Grid, makeStyles, Typography } from '@material-ui/core';
import React, { Fragment, useEffect, useState } from 'react';
import type { Element } from 'react';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import { getAllOffers, updateOfferStep } from '../redux/Actions';
import { OFFER_STEPS } from 'shared/Consts';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { toastr } from 'react-redux-toastr';
import { OFFER_ERROR_MESSAGES } from 'shared/Messages';
import { PageWrapper } from '../../../components/PageWrapper';
import withCustomAuthenticator from 'containers/Authenticator/withCustomAuthenticator';
import { Widget } from 'components/Widget';
import { mappingCounterData } from '../../../helpers/Offer';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import CounterOfferModal from 'components/Offers/CounterOfferModal';
import { OffersTable } from './OffersTable';
import { ConfirmDialog } from 'components/ConfirmDialog';
import ConfirmTransferModal from 'components/BuyerOfferStep/Step500/ConfirmTransferModal';
import { ViewOfferDialog } from './ViewOfferDialog';
import ConfirmInspectionDialog from 'components/BuyerOfferStep/Step600/ConfirmInspectionDialog';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '56px',
  },
  widget: {
    padding: 0,
  },
}));

type PropsWithoutRedux = {};

type Props = {
  fetchOffers: Function,
  offers: Array<Object>,
  isLoading: boolean,
  saveOfferStep: Function,
  isUpdatedStep: boolean,
  error: Object,
  isFormLoaded: boolean,
};

const OfferList = (props: Props): Element<typeof Fragment> => {
  const {
    fetchOffers,
    offers,
    isLoading,
    saveOfferStep,
    isUpdatedStep,
    error,
    isFormLoaded,
  } = props;

  const classes = useStyles();
  const [openOfferDialog, setOpenOfferDialog] = useState(false);
  const [openCounterOfferDialog, setOpenCounterOfferDialog] = useState(false);
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);
  const [confirmDialogProps, setConfirmDialogProps] = useState({
    children: '',
    onAgree: () => '',
  });
  const [openConfirmTransfer, setOpenConfirmTransfer] = useState(false);
  const [openScheduleInspection, setOpenScheduleInspection] = useState(false);
  const [selectedOffer, setSelectedOffer] = useState(undefined);

  useEffect(() => {
    fetchOffers();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (isUpdatedStep) {
      fetchOffers();
      handleClickCloseDialog();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isUpdatedStep]);

  useEffect(() => {
    const { message } = error || {};

    if (message) {
      toastr.error(OFFER_ERROR_MESSAGES.saveFailure, message);
    }
  }, [error]);

  const handleViewOffer = offer => () => {
    setSelectedOffer(offer);
    setOpenOfferDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenOfferDialog(false);
  };

  const getConfirmContent = type =>
    `Are you sure? - This action cannot be reverted. Do you want to ${type} the offer?`;

  const handleOpenAcceptConfirm = offerId => {
    setOpenConfirmDialog(true);
    setConfirmDialogProps({
      children: getConfirmContent('accept'),
      onAgree: () => {
        setOpenConfirmDialog(false);
        handleAccept(offerId);
      },
    });
  };

  const handleOpenDeclineConfirm = offerId => {
    setOpenConfirmDialog(true);
    setConfirmDialogProps({
      children: getConfirmContent('decline'),
      onAgree: () => {
        setOpenConfirmDialog(false);
        handleDecline(offerId);
      },
    });
  };

  const handleAccept = offerId => {
    saveOfferStep(offerId, { new_step: OFFER_STEPS.buyerAcceptCounterOffer });
  };

  const handleDecline = offerId => {
    saveOfferStep(offerId, { new_step: OFFER_STEPS.buyerDecline });
  };

  const handleClickOpenCounterDialog = offer => {
    setSelectedOffer(offer);
    setOpenOfferDialog(false);
    setOpenCounterOfferDialog(true);
  };

  const handleClickCloseDialog = ({ isReload } = {}) => {
    setSelectedOffer();
    setOpenCounterOfferDialog(false);
    setOpenOfferDialog(false);
    setOpenConfirmDialog(false);

    if (isReload) {
      fetchOffers();
    }
  };

  const handleOpenConfirmTransfer = offer => {
    setOpenConfirmTransfer(true);
    setSelectedOffer(offer);
  };

  const handleScheduleInspection = offer => {
    setOpenScheduleInspection(true);
    setSelectedOffer(offer);
  };

  const mappingOffers = offers ? offers.map(mappingCounterData) : [];
  const mappingSelectedOffer =
    selectedOffer && mappingCounterData(selectedOffer);

  return (
    <PageWrapper>
      <Grid container justify="center">
        <Grid item xs={12}>
          <Widget className={classes.widget}>
            <BlockUi
              tag="div"
              blocking={isLoading}
              loader={
                <Loader active type="ball-pulse" color="rgba(231,28,99,1)" />
              }
              className={classes.container}
            >
              {!_isEmpty(offers) && (
                <OffersTable
                  offers={mappingOffers}
                  onViewOffer={handleViewOffer}
                  handleOpenConfirmTransfer={handleOpenConfirmTransfer}
                  handleClickOpenCounterDialog={handleClickOpenCounterDialog}
                  handleViewOffer={handleViewOffer}
                  handleScheduleInspection={handleScheduleInspection}
                />
              )}
              {_isEmpty(offers) && !isLoading && (
                <Grid container className={classes.notFound}>
                  <Typography variant="h3">No offer found</Typography>
                </Grid>
              )}
            </BlockUi>
          </Widget>
        </Grid>
      </Grid>
      <CounterOfferModal
        offer={mappingSelectedOffer}
        open={openCounterOfferDialog}
        handleClickCloseDialog={handleClickCloseDialog}
        isFormLoaded={isFormLoaded}
      />
      <ViewOfferDialog
        open={openOfferDialog}
        offer={mappingSelectedOffer}
        onClose={handleCloseDialog}
        handleApproval={handleOpenAcceptConfirm}
        handleDecline={handleOpenDeclineConfirm}
        handleCounter={handleClickOpenCounterDialog}
        isSubmitting={isLoading}
      />
      <ConfirmDialog
        open={openConfirmDialog}
        onClose={() => setOpenConfirmDialog(false)}
        {...confirmDialogProps}
      />
      <ConfirmTransferModal
        open={openConfirmTransfer}
        handleClose={() => setOpenConfirmTransfer(false)}
        offer={mappingSelectedOffer}
      />
      <ConfirmInspectionDialog
        open={openScheduleInspection}
        handleClose={() => setOpenScheduleInspection(false)}
        offer={mappingSelectedOffer}
      />
    </PageWrapper>
  );
};

const structuredSelector = createStructuredSelector({
  offers: state => _get(state, ['buyerOffer', 'offers']),
  isLoading: state => _get(state, ['buyerOffer', 'isLoading']),
  isUpdatedStep: state => _get(state, ['buyerOffer', 'isUpdatedStep']),
  error: state => _get(state, ['buyerOffer', 'error']),
  isFormSaved: state => _get(state, ['form', 'isFormSaved']),
  isFormLoaded: state => _get(state, ['form', 'isFormLoaded']),
});

const mapDispatchToProps = {
  fetchOffers: getAllOffers,
  saveOfferStep: updateOfferStep,
};

export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(
  withErrorBoundary(withCustomAuthenticator(OfferList)),
): Element<PropsWithoutRedux>);
