// @flow
import type { Element } from 'react';
import {
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
  makeStyles,
} from '@material-ui/core';
import React from 'react';
import _get from 'lodash/get';
import { useOrder, useSelectRow } from '../../../hooks';
import { formatPrice } from '../../../helpers/number';
import { Header } from 'components/ListTable';
import { headerCells } from './helpers';
import ActionButtons from './ActionButtons';

const useStyles = makeStyles(theme => ({
  row: {
    ...theme.typography.h5,
    color: theme.palette.text.secondaryDark,
    '&&:hover ,&.Mui-selected': {
      backgroundColor: theme.palette.background.green,
    },
    '& td': {
      borderRight: '1px solid #E0E0E0',
    },
    '& td:last-child': {
      borderRight: '0',
    },
  },
}));

type Props = {
  offers: Object[],
  onViewOffer: Function,
  handleOpenConfirmTransfer: Function,
  handleClickOpenCounterDialog: Function,
  handleViewOffer: Function,
  handleScheduleInspection: Function,
};

export const OffersTable = ({
  offers,
  onViewOffer,
  handleOpenConfirmTransfer,
  handleClickOpenCounterDialog,
  handleViewOffer,
  handleScheduleInspection,
}: Props): Element<typeof TableContainer> => {
  const classes = useStyles();
  const { order, orderBy, handleRequestSort } = useOrder('OfferNo');
  const { selected, isSelected, handleSelect, handleSelectAll } =
    useSelectRow('OfferNo');

  const handleSelectAllClick = event => {
    handleSelectAll(event.target.checked, offers);
  };

  const getOfferInfo = offer => ({
    offerId: _get(offer, 'OfferId', ''),
    offerNo: _get(offer, 'OfferNo', ''),
    price: formatPrice(_get(offer, 'MappingCounter.OfferPrice', '')),
    funding: _get(offer, 'Funding.PurchaseType', ''),
    inspectionContingency: _get(
      offer,
      'MappingCounter.InspectionContingency',
      '',
    ),
    saleOfHomeContingency: _get(
      offer,
      'MappingCounter.SaleOfHomeContingency',
      '',
    ),
    status: _get(offer, 'Step.messages.buyer', ''),
  });

  return (
    <TableContainer>
      <Table aria-labelledby="tableTitle" size="small">
        <Header
          cells={headerCells}
          numSelected={selected.length}
          order={order}
          orderBy={orderBy}
          onSelectAllClick={handleSelectAllClick}
          onRequestSort={handleRequestSort}
          rowCount={offers.length}
        />
        <TableBody>
          {offers.map((offer, index) => {
            const {
              offerId,
              offerNo,
              price,
              funding,
              inspectionContingency,
              saleOfHomeContingency,
              status,
            } = getOfferInfo(offer);
            const isItemSelected = isSelected(offerId);

            return (
              <TableRow
                hover
                onClick={() => handleSelect(offerId)}
                role="checkbox"
                aria-checked={isItemSelected}
                tabIndex={-1}
                key={offerId}
                selected={isItemSelected}
                classes={{
                  root: classes.row,
                }}
              >
                <TableCell align="center" padding="none">
                  <Checkbox color="primary" checked={isItemSelected} />
                </TableCell>
                <TableCell padding="none" align="center">
                  {offerNo}
                </TableCell>
                <TableCell align="center" padding="none">
                  {price}
                </TableCell>
                <TableCell align="center" padding="none">
                  {funding}
                </TableCell>
                <TableCell align="center" padding="none">
                  {inspectionContingency}
                </TableCell>
                <TableCell align="center" padding="none">
                  {saleOfHomeContingency}
                </TableCell>
                <TableCell align="center" padding="none">
                  {status}
                </TableCell>
                <TableCell align="center" sx={{ pr: 3 }} padding="none">
                  <ActionButtons
                    offer={offer}
                    handleOpenModalConfirmTransfer={handleOpenConfirmTransfer}
                    handleCounter={handleClickOpenCounterDialog}
                    handleViewOffer={handleViewOffer(offer)}
                    handleScheduleInspection={handleScheduleInspection}
                  />
                </TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
